$control-margin: 0.5em;

.ol-scale-line {
  background: rgba(255, 255, 255, 0.3);
  bottom: 10px;
  right: 40px;
  left: initial;

  .ol-scale-line-inner {
    color: black;
    border: 1px solid black;
    border-top: none;
  }
}

.ol-full-screen {
  right: auto;
  left: $control-margin;
  top: 60px;
}
