
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        































































$sidemenu-width: 200px;
$body-height: calc(100vh - #{$navbar-height});

html {
  overflow-x: hidden;
  overflow-y: scroll;
  text-rendering: optimizeLegibility;
  text-size-adjust: 100%;
}

html,
body,
#app {
  min-height: $body-height;
}

.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 25;
}

.side-menu {
  width: $sidemenu-width;
  height: 100vh;
  position: fixed;
  top: 0px;
  z-index: 30;
  transition: 0.3s;
}

.alternative-side-menu-shader {
  z-index: 29;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.2);
}

.site-notice {
  position: absolute;
  top: $navbar-height;
  width: 100%;
  z-index: 20;
  box-shadow: 0 5px 10px 0px rgba(10, 10, 10, 0.5);
}

.page-content {
  min-height: 100vh;
  padding-top: $navbar-height;
  display: flex;
  flex-flow: column;
}

@media screen and (max-width: $tablet) {
  .side-menu {
    left: -$sidemenu-width;
  }

  .alternative-side-menu {
    left: 0;
  }

  .page-content {
    margin-left: 0;
  }

  .section {
    padding: 0.75rem !important;
  }

  .box:not(:last-child),
  .feed-card {
    margin-bottom: 0.75rem !important;
  }
}

@media screen and (min-width: $tablet) and (max-width: $desktop) {
  .side-menu {
    left: -$sidemenu-width;
  }

  .alternative-side-menu {
    left: 0;
  }

  .page-content {
    margin-left: 0;
  }
}

@media screen and (min-width: $desktop) and (max-width: $widescreen) {
  .page-content {
    margin-left: $sidemenu-width;
  }

  .site-notice {
    padding-left: $sidemenu-width;
  }
}

@media screen and (min-width: $widescreen) and (max-width: $fullhd) {
  .page-content {
    margin-left: $sidemenu-width;
  }

  .site-notice {
    padding-left: $sidemenu-width;
  }
}

@media screen and (min-width: $fullhd) {
  .page-content {
    margin-left: $sidemenu-width;
  }

  .site-notice {
    padding-left: $sidemenu-width;
  }
}

.router-view {
  flex-grow: 1;
}

@media print {
  /* print styles go here */
  .page-content {
    padding-top: 0;
  }

  html {
    font-size: 12px !important;
  }
}
